body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#1a1a1a}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.navbar{display:grid;background:#1a1a1a;padding:2em;grid-template-columns:25% 65% 10%;grid-template-rows:auto auto auto;grid-template-areas:"title blank logout"}.navbar__div{font-family:"ZCOOL KuaiLe", cursive;font-size:3em;color:#f90;grid-area:title}.navbar__img{grid-area:logout;max-width:75px;justify-self:end;cursor:pointer}

.login{display:grid;width:30%;height:40em;margin:auto;background:#2d2d2d;border-radius:4pt;grid-template-columns:100%;grid-template-rows:4fr 1fr 1fr 1fr 2fr;justify-content:center;text-align:center}.login__div{display:grid;grid-template-columns:100%;grid-template-rows:1fr 1fr 1fr}.login__h1{color:#f90;font-size:3em;font-family:"Roboto", sans-serif}.login__input{width:60%;height:50%;margin:auto auto 7% auto;border-radius:4pt;border:none;outline:none}.login__input--error{background-color:#f39292}.login__input::-webkit-input-placeholder{padding-left:8px;color:#a2a2a2;font-size:1.2em}.login__input:-ms-input-placeholder{padding-left:8px;color:#a2a2a2;font-size:1.2em}.login__input::placeholder{padding-left:8px;color:#a2a2a2;font-size:1.2em}.login__input--error::-webkit-input-placeholder{padding-left:8px;color:white;font-size:1.2em}.login__input--error:-ms-input-placeholder{padding-left:8px;color:white;font-size:1.2em}.login__input--error::placeholder{padding-left:8px;color:white;font-size:1.2em}.login__button{background-color:#f90;color:white;font-size:1.2em;border-radius:4pt;border:none;width:30%;height:65%;margin:auto;cursor:pointer}.login__p{color:white;margin:5% auto 5% auto}.login__a{text-decoration:none;color:#f90;font-size:1.8em;font-weight:100}

.register{display:grid;width:30%;height:40em;margin:auto;background:#2d2d2d;border-radius:4pt;grid-template-columns:100%;grid-template-rows:1fr 4fr;justify-content:center;text-align:center}.register__h1{color:#f90;font-size:2.5em;margin-top:8%;font-family:"Roboto", sans-serif}.register__input{width:65%;height:5%;margin:auto auto 4% auto;border-radius:4pt;border:none;padding:5px}.register__input--error{background-color:#f39292}.register__input::-webkit-input-placeholder{padding-left:8px;color:#a2a2a2;font-size:1.2em}.register__input:-ms-input-placeholder{padding-left:8px;color:#a2a2a2;font-size:1.2em}.register__input::placeholder{padding-left:8px;color:#a2a2a2;font-size:1.2em}.register__button{background-color:#f90;color:white;font-size:1.2em;border-radius:4pt;border:none;width:35%;height:12%;margin:5% auto auto auto;cursor:pointer;justify-self:start}.register__inputWrapper{margin-top:10%}.register__iconWrapper{position:absolute;margin:15px 0px 0px 10px;cursor:pointer;padding:15px 0px 0px 10px}.register__icon{width:50px;height:2px;background-color:white;position:relative;margin:0 auto;z-index:9}.register__icon:before,.register__icon:after{width:10px;height:2px;content:"";position:absolute;background-color:white}.register__icon:before{-webkit-transform:rotate(-40deg);transform:rotate(-40deg);right:40px;bottom:4px}.register__icon:after{-webkit-transform:rotate(40deg);transform:rotate(40deg);right:40px;top:4px}

.ListModal{display:grid;width:60%;margin:10px auto;background:#2d2d2d;color:white;border-radius:4pt;grid-template-columns:100%;grid-template-rows:1fr 1fr 8fr;pointer-events:none;opacity:0;position:absolute;z-index:2;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.ListModal--open{opacity:1;pointer-events:all}.ListModal__input{height:20px;width:90%;margin:20px auto 20px auto}.ListModal__line{background-color:#f90;height:2px;width:95%;margin:20px auto auto auto}.ListModal__taskName{width:95%;height:20px;background-color:#2d2d2d;outline:none;border:none;color:white;border-bottom:1pt solid #f90;font-size:18px}.ListModal__tasks{width:100%;height:100%}.ListModal__checkbox{width:15px}.ListModal__button{color:white;border:none;border-radius:8px;width:100px;height:44px;font-size:22px;margin:20px;cursor:pointer}.ListModal__button--darkOrange{background-color:#ff3d00;float:right}.ListModal__button--orange{background-color:#f90;float:right}.ListModal__button--gray{color:#f90;background-color:#2d2d2d}.ListModal__task{margin:0px 20px 20px 20px}

.ListItem{display:grid;width:100%;height:60px;margin:10px auto;background:#2d2d2d;color:white;border-radius:4pt;grid-template-columns:20% 20% 40%;grid-column-gap:5%;-webkit-column-gap:5%;column-gap:5%;grid-template-rows:50%;justify-content:center;align-content:center;text-align:center;cursor:pointer;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.ListItem__wrapper{display:grid;grid-template-columns:40% 40% 20%;justify-content:end;text-align:end}.ListItem__name{font-weight:bold;text-align:start}.ListItem__date{font-style:italic}

.ListWrapper{display:grid;width:50%;margin:auto;background:#1a1a1a;color:white;border-radius:4pt;grid-template-columns:100%;grid-template-rows:1fr repeat(1fr)}.ListWrapper__input{width:30%;height:20px;margin:auto auto 4% auto;border-radius:2pt;border:none;padding:5px}.ListWrapper__select{width:15%;height:35px;margin:auto auto 4% auto;float:right;border-radius:2pt;border:none;padding:5px}.ListWrapper__addButton{background-color:white;position:absolute;z-index:1;border-radius:100%;width:52px;height:52px;right:25%;bottom:0;cursor:pointer}.ListWrapper__addButton:before,.ListWrapper__addButton:after{width:42px;height:3px;content:"";position:absolute;background-color:#f90;top:25px;left:5px}.ListWrapper__addButton:before{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}.ListWrapper__addButton:after{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.ListWrapper__loading{display:inline-block;width:200px;height:200px;margin:200px auto}.ListWrapper__loading:after{content:" ";display:block;width:200px;height:200px;margin:8px;border-radius:50%;border:6px solid #f90;border-color:#f90 transparent #f90 transparent;-webkit-animation:loading 1.2s linear infinite;animation:loading 1.2s linear infinite}@-webkit-keyframes loading{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.ListWrapper__info{color:#f90;background-color:#1a1a1a;text-align:center;font-weight:600}

.alert{display:grid;width:35%;height:5%;position:absolute;z-index:10;left:0;right:0;margin:25% auto;border-radius:4pt;grid-template-columns:100%;grid-template-rows:50%;justify-content:center;text-align:center;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.alert--danger{background:#f0b4b4;color:black}.alert--success{background:lightgreen;color:black}.alert--info{background:lightblue;color:black}

